"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import { LoaderCircle } from "lucide-react";

type Question = {
  question: string;
  options: string[];
  answer: string; // Optional depending on your use case
};

export default function QuizPage() {
  const { id } = useParams();
  const [questions, setQuestions] = useState<Question[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchQuestions = async () => {
      try {
        const res = await fetch(`/api/summaries/${id}/quiz`);
        const data = await res.json();

        if (!res.ok) {
          throw new Error(data.error || "Failed to load quiz");
        }

        setQuestions(data.questions);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchQuestions();
  }, [id]);

  if (loading) {
    return (
      <div className="p-6 flex items-center justify-center h-full text-gray-600">
        <LoaderCircle className="animate-spin mr-2" />
        Loading quiz...
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-6 text-red-600">
        Error loading quiz: {error}
      </div>
    );
  }

  return (
    <div className="p-6 max-w-2xl mx-auto">
      <h1 className="text-2xl font-bold mb-6 text-[#006747]">ðŸ§  Test Your Knowledge</h1>

      {questions.length === 0 ? (
        <p className="text-gray-600">No questions generated for this summary.</p>
      ) : (
        <div className="space-y-6">
          {questions.map((q, idx) => (
            <div key={idx} className="border rounded-lg p-4 bg-white shadow-sm">
              <h2 className="font-medium mb-2">{idx + 1}. {q.question}</h2>
              <ul className="space-y-2">
                {q.options.map((opt, i) => (
                  <li key={i}>
                    <label className="inline-flex items-center gap-2 cursor-pointer">
                      <input type="radio" name={`q${idx}`} value={opt} className="form-radio" />
                      {opt}
                    </label>
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
